.slideshow{
    width: 100%;
    height: 415px;


    &__nav{
        position: relative;
        width: 100%;

        &__chevron{
            fill: white;
            width: 46.68px;
            height: 79.2px;
            position: absolute;
            top: 180px;

            &--prev{
                left: 2%;
            }

            &--next{
                right: 2%;
            }

            &:hover{
                cursor: pointer;
            }
        }

        &__count{
            font-family: Montserrat;
            font-size: 18px;
            font-style: normal;
            color: #FFF;
            position: absolute;
            top: 370px;
            left: 49%;
        }
    }

    &__img--container{
        width: 100%;
        height: 415px;
        border-radius: 25px;
        overflow: hidden;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    &__img{
        border-radius: 25px;
        object-fit: cover;
        object-position: center;
        height: 415px;
        width: 100%;

    }
}

@media screen and (max-width: 768px){
    .slideshow{
        height: 255px;
    
    
        &__nav{
            position: relative;
    
            &__chevron{
                fill: white;
                width: 24px;
                height: 24px;
                top: 120px;
    
                &--prev{
                    left: 2%;
                }
    
                &--next{
                    right: 2%;
                }

            }
    
            &__count{
                font-size: 10px;
                top: 370px;
                left: 49%;
            }
        }
    
        &__img--container{
            width: 100%;
            height: 255px;
            border-radius: 10px;
        }
    
        &__img{
            border-radius: 25px;
            object-fit: cover;
            object-position: center;
            height: 255px;
            width: 100%;
    
        }
    }
    
}